import React from 'react'

class App extends React.Component{
    state = {
        data: [10]
    }
    componentDidMount(){
        fetch('http://54.80.119.89:8000/api/districts/')
        .then(response => response.json())
      .then(data => this.setState({data: data}));
    }
    render(){
        return(
            <div>
                {this.state.data.map(dis => {return(dis.name)})}
                <h1>Home</h1>
            </div>
        )
    }
}


export default App;